@import url(https://fonts.googleapis.com/css?family=Poppins);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: green;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.display {
  position: absolute;
  bottom: 0;
  left: 0;
  display: flex;
}

.bingo-ball {
    font-family: 'Poppins';
    font-size: 50px;
    border-radius: 50%;
    height: 100px;
    width: 100px; 
    line-height:100px;
    margin-top: auto; 
    background: radial-gradient(circle at 80px 80px,rgb(146, 137, 87), #ffffff );
    text-align: center;
    vertical-align: middle;  
    margin-right: 10px;
  }

.bingo-ball.first {
  font-size: 100px;
  height: 150px;
  width: 150px;
  line-height:150px;
  background: radial-gradient(circle at 120px 120px, rgb(146, 137, 87), #ffffff);
}
